@import url(https://fonts.googleapis.com/css?family=Vollkorn&display=swap);
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

.nav-style ul {
  display: flex;
  list-style: none;
  justify-content: center;
  text-decoration: none;
}

.nav-style li {
  margin-right: 20px;
  color: white;
}

.nav-style a {
  color: white;
  text-decoration: underline !important;
}


.App {
  text-align: center;
  /* background-color: #9bbede; */
}

.home{
  background-image: url('https://images.unsplash.com/photo-1490131784822-b4626a8ec96a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80');
  background-repeat: no-repeat;
  background-position: center; /* Center the image */
  height: 100vh;
  display: grid;
  justify-content: center;
  text-align: center;
  background-size: cover; /* Resize the background image to cover the entire container */
  background-position-y: auto;
}

.hometext{
  font-size: 30px;
  text-align: center;
  font-family: 'Vollkorn', serif;

}

.login {
  font-size: 30px;
  margin-top: -80px;
  text-align: center;
  font-family: 'Vollkorn', serif;
}

input {
	box-shadow:inset 1px 0px 3px 0px #91b8b3;
	background-color:#768d87;
	border-radius:19px;
	border:1px solid #566963;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Verdana;
	font-size:17px;
	font-style:italic;
	padding:18px 47px;
	text-decoration:none;
}
input:hover {
	background-color:#6c7c7c;
}
input:active {
	position:relative;
	top:1px;
}

::-webkit-input-placeholder { 
  color: white;
  opacity: 0.8;
}

::-moz-placeholder { 
  color: white;
  opacity: 0.8;
}

::-ms-input-placeholder { 
  color: white;
  opacity: 0.8;
}

::placeholder { 
  color: white;
  opacity: 0.8;
}



